<template>
  <div class="box">
    <van-tabs v-model="active" title-active-color="#FF4A54"
     :swipe-threshold="threshold">
      <van-tab v-for="(item,index) in navs" :key="index" :title="item.name"
      >
      <div class="navs-c">
        <div :class="{active:cur == n}" v-for="(i,n) in item.children" :key="n" @click="changecur(n)">{{ i }}</div>
      </div>
      </van-tab>
    </van-tabs>
    <div class="goods-con">
      <goods :list="lists1" />
    </div>
     <div class="more">没有更多商品了</div>
  </div>
</template>

<script>
import goods from "@/web/components/Goods";
export default {
  components: {
    goods
  },
  data() {
    return {
      threshold:5,
      cur:0,
      active: 0,
      navs: [
        {
          name: "热门推荐",
          children: ["套装组合1", "面部1", "美甲1"]
        },
        {
          name: "美容美发",
          children: ["套装组合2", "面部2", "美甲2"]
        },
        {
          name: "整形塑身",
          children: ["套装组合3", "面部3", "美甲3"]
        },
        {
          name: "养生调理",
          children: ["套装组合4", "面部4", "美甲4"]
        },
        {
          name: "日用品",
          children: ["套装组合5", "面部5", "美甲5"]
        }
      ],
      lists1: [
        {
          id: 1,
          name: "俄罗斯美甲（体验价）",
          image: require("@/assets/homepages/img1.png"),
          price_before: "88.00",
          price_current: "88.00",
          btn: "马上抢"
        },
        {
          id: 2,
          name: "蒙巴萨涂抹式去眼袋（体验价）",
          image: require("@/assets/homepages/img2.png"),
          price_before: "19.90",
          price_current: "88.00",
          btn: "马上抢"
        },
        {
          id: 3,
          image: require("@/assets/homepages/img3.png"),
          name: "第八周ALB血清蛋白护肤（体验价）",
          price_before: "50.00",
          price_current: "88.00",
          btn: "马上抢"
        },
        {
          id: 4,
          name: "360身体部位塑性（体验价）",
          image: require("@/assets/homepages/img3.png"),
          price_before: "88.00",
          price_current: "188.00",
          btn: "马上抢"
        }
      ]
    }
  },
  methods: {
    changecur(n) {
      this.cur = n
    }
  }
};
</script>

<style lang="scss">
.box {
  // border: 1px solid;
 
  .navs-c{
    height: 80px;
    display: flex;
    background-color: #FFF;
    align-items: center;
    border-top:thin solid #D7D7D7;
    box-shadow:0px 1px 7px rgba(0,0,0,0.1);
    &>div{
      margin-left: 20px;
      color: #555;
      font-size: 22px;
      height: 44px;
      border: 2px solid  #D7D7D7;
      padding: 7px 28px;
      box-sizing: border-box;
      line-height: 30px;
      border-radius: 22px;
      &.active{
        border: 2px solid transparent;
        background-color: #FF4A54;
        color:#FFF;
      }
    }
  }
  .goods-con{
    margin: 0 24px;
  }
}
</style>



